import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-load-to-social-media',
  templateUrl: './load-to-social-media.component.html',
  styleUrls: ['./load-to-social-media.component.scss']
})
export class LoadToSocialMediaComponent implements OnInit {
  loginData: any;

  constructor(public router: Router,) { }

  ngOnInit() {
    this.loginData = JSON.parse(localStorage.getItem("login"));
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
