import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../../utilities/imageupload'
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
import * as moment from 'moment';
@Component({
  providers: [Beyond],
  selector: 'app-list-promotion',
  templateUrl: './list-promotion.component.html',
  styleUrls: ['./list-promotion.component.scss']
})
export class ListPromotionComponent implements OnInit {
  loginData: any;
  promotionListDetails: any;
  promotionListResponse: boolean;
  All: boolean;
  searchVendor: FormGroup;
  searchPromotion: FormGroup;
  promotion: any;
  bill: any;
  promotionStatus: boolean;
  showData: boolean;
  listOfPromotionsData: any;
  PromotionSearch: any;
  proId: any;
  min: any = new Date();
  startMin: any = moment().format('YYYY-MM-DD');
  startMax: any;
  endMin: any = moment().format('YYYY-MM-DD');
  constructor(public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,

  ) {

  }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.getPromotionList()
    this.proId = this.loginData.loginCode;

    this.searchPromotion = this.formBuilder.group({
      startDate: [null],
      endDate: [null],
      status: ['All']
    })
  }

  // Search Work Order Starts
  search() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    let obj = {
      "startDate": this.searchPromotion.value.startDate,
      "endDate": this.searchPromotion.value.endDate,
      "status": this.searchPromotion.value.status,
    }
    console.log(obj)
    if (this.searchPromotion.value.startDate !== null && this.searchPromotion.value.endDate == null && this.searchPromotion.value.status == 'All') {
      this.http.get(Global.url + '/getStartDateSearch/' + this.searchPromotion.value.startDate + "/" + this.proId, options)
        .subscribe(data => {
          this.PromotionSearch = data.json()
          console.log("status1" + this.searchPromotion.value.startDate)
          console.log("status1" + this.searchPromotion.value.endDate)
          if (this.PromotionSearch.status == 200) {
            this.promotionListDetails = this.PromotionSearch.response;
            this.promotionListResponse = true;
          } else {
            this.promotionListResponse = false;
          }
        })
    }
    else if (this.searchPromotion.value.endDate !== null && this.searchPromotion.value.startDate == null && this.searchPromotion.value.status == 'All') {
      this.http.get(Global.url + '/getEndDateSearch/' + this.searchPromotion.value.endDate + "/" + this.proId, options)
        .subscribe(data => {
          this.PromotionSearch = data.json()
          console.log("status2" + this.searchPromotion.value.startDate)
          console.log("status2" + this.searchPromotion.value.endDate)
          if (this.PromotionSearch.status == 200) {
            this.promotionListDetails = this.PromotionSearch.response;
            this.promotionListResponse = true;
          } else {
            this.promotionListResponse = false;
          }
        })
    }
    else if (this.searchPromotion.value.startDate !== null && this.searchPromotion.value.endDate !== null && this.searchPromotion.value.status == 'All') {
      this.http.get(Global.url + '/getStartDateEndDateSearch/' + this.searchPromotion.value.startDate + "/" + this.searchPromotion.value.endDate + "/" + this.proId, options)
        .subscribe(data => {
          this.PromotionSearch = data.json()
          console.log("status3" + this.searchPromotion.value.startDate)
          console.log("status3" + this.searchPromotion.value.endDate)
          if (this.PromotionSearch.status == 200) {
            this.promotionListDetails = this.PromotionSearch.response;
            this.promotionListResponse = true;
          } else {
            this.promotionListResponse = false;

          }
        })
    }
    else if (this.searchPromotion.value.startDate !== null && this.searchPromotion.value.endDate !== null && this.searchPromotion.value.status !== 'All') {
      this.http.get(Global.url + '/getStartDateEndDateStatusSearch/' + this.searchPromotion.value.startDate + "/" + this.searchPromotion.value.endDate + "/" + this.searchPromotion.value.status + "/" + this.proId, options)
        .subscribe(data => {
          this.PromotionSearch = data.json()
          console.log("status4" + this.searchPromotion.value.startDate)
          console.log("status4" + this.searchPromotion.value.endDate)
          console.log("status4" + this.searchPromotion.value.status)
          if (this.PromotionSearch.status == 200) {
            this.promotionListDetails = this.PromotionSearch.response;
            this.promotionListResponse = true;
          } else {
            this.promotionListResponse = false;

          }
        })
    }
    else if (this.searchPromotion.value.status !== 'All' && this.searchPromotion.value.startDate == null && this.searchPromotion.value.endDate == null) {
      this.http.post(Global.url + '/searchPromotionStatus/' + this.searchPromotion.value.status + '/' + this.proId, options)
        .subscribe(data => {
          this.PromotionSearch = data.json()
          console.log("status5" + this.searchPromotion.value.status)
          if (this.PromotionSearch.status == 200) {
            this.promotionListDetails = this.PromotionSearch.response;
            this.promotionListResponse = true;
          } else {
            this.promotionListResponse = false;

          }
        })
    }
    else if (this.searchPromotion.value.status == 'All' || this.searchPromotion.value.startDate == null && this.searchPromotion.value.endDate == null) {
      console.log("status6" + this.searchPromotion.value.status)
      this.getPromotionList()
    }
    else if (this.searchPromotion.value.status !== 'All' && this.searchPromotion.value.startDate !== null && this.searchPromotion.value.endDate == null) {
      alert('please select End Date')
    }
    else if (this.searchPromotion.value.status !== 'All' && this.searchPromotion.value.startDate == null && this.searchPromotion.value.endDate !== null) {
      alert('please select Start Date')
    }
    else {
      this.promotionListResponse = false;
    }


  }
  // Search Work Order Ends
  onStartDateSelected(startDate) {
    this.endMin = startDate;
  }

  onEndDateSelected(endDate) {
    this.startMax = endDate;
  }


  reset() {
    this.searchPromotion.get('startDate').reset();
    this.searchPromotion.get('endDate').reset();
    this.searchPromotion.get('status').setValue('All');

    this.getPromotionList()
  }

  // promotionSelection(x) {
  //   console.log("x" + x)
  //   if (x == "All") {
  //     this.getPromotionList()
  //   }
  // }

  getPromotionList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfPromotions' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.promotionListDetails = response.response;
          this.promotionListResponse = true;
        }
        else {
          this.promotionListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  addPromotion() {
    this.router.navigate(['/promotion/addPromotion'])
  }
  viewPromotion(data) {
    this.router.navigate(['/promotion/viewPromotion', data.promotionCode])
  }
  editPromotion(data) {
    this.router.navigate(['/promotion/editPromotion', data.promotionCode])
  }
  deleteInventory() {

  }



}
