import { Component, OnInit, ViewChild } from "@angular/core";
import { Beyond } from "../../../../utilities/beyond";
import { Router, ActivatedRoute } from "@angular/router";
import { ImageUpload } from "../../../../utilities/imageupload";
import { Global } from "../../../../global/global";
import { Http, Headers, RequestOptions } from "@angular/http";
import { ToastrService } from "ngx-toastr";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
  FormArray
} from "@angular/forms";

import * as moment from 'moment';
@Component({
  providers: [Beyond, ImageUpload],
  selector: 'app-add-promotion',
  templateUrl: './add-promotion.component.html',
  styleUrls: ['./add-promotion.component.scss']
})
export class AddPromotionComponent implements OnInit {
  addPromotion: FormGroup;
  loginEmailData: any;
  prozData: any;
  loginData: any;
  promotionCode: any;
  promotions: boolean;
  promotionList: any;
  vendorList: any;
  vendors: boolean;
  percentage: any = 0;
  imagePreview: string = "../assets/img/no-image.png";
  file2Upload: any
  documentName: any;
  img: boolean = false;
  @ViewChild('imageUpload')
  imageUploadVariable: any;
  progressShow: boolean = false;
  progressHide: boolean = false;
  removeFlag: boolean = false;
  vendorListResponse: boolean;
  submitted: boolean;
  // endMin: any;
  // startMax: any;
  min: any = new Date();
  startMin: any = moment().format('YYYY-MM-DD');
  startMax: any;
  endMin: any = moment().format('YYYY-MM-DD');
  urls = [];
  imagePreviewDocument: string = "assets/img/no-image.png";
  galleryImages = [];
  filesToUpload: any = [];
  imageArray: any = []
  progressShow1: boolean = false;
  uploadFlag: boolean = false;
  progressHide1: boolean = false;
  displyFlag: boolean = false;
  img1: boolean = false;
  percentage1: any = 0;
  file2Upload1: any;
  file: any;
  arr: any = [];
  imageuploadstatus: boolean;
  chosseTypecount: number;
  fileName: string;
  formData: FormData;
  removedPture: any;
  result: any = [];
  imageNameData: any = [];
  newArray: any = [];
  number: any;
  renamedarray: any;
  orginalArray: any = [];
  value1: any;
  imageName: any = [] = [];
  newValue: any;
  resultData: { index: string; value: any; }[];
  imageNames: any = [];
  speArray: string;
  imageValue: any = [];
  value: any;
  promotionMarketingMediaType: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"));
    this.prozData = this.loginData.loginCode;
    this.loginEmailData = this.loginData.emailId;
    this.getPromotionCode(),
      this.listPromotionMarketingMediaType();
    this.getVendorList(),
      this.addPromotion = this.formBuilder.group({
        promotionCode: ['', Validators.required],
        proId: ['', Validators.required],
        vendorName: [''],
        promotionName: ['', Validators.required],
        startDate: ['', Validators.required],
        endDate: ['', Validators.required],
        type: ['', Validators.required],
        mode: ['', Validators.required],
        recurring: ['', Validators.required],
        promotionFees: ['', Validators.required],
        promotionValues: ['', Validators.required],
        marketingMediaType: ['', Validators.required],
        totalTicket: ['', Validators.required],
        description: ['', Validators.required],
        promotionImage: [''],
        status: ['']
      })
  }


  // AUto Genrat
  getPromotionCode() {
    console.log("Promotion Code ");
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + "/generatePromotionCode", options).subscribe(
      data => {
        var response = data.json();
        this.promotionCode = response.promotionCode;
        console.log("Promotion Code " + this.promotionCode);
      },
      error => {
        this.toastr.warning("Unable to connect to server");
      }
    );
  }
  // AUto Genrat
  get f() {
    return this.addPromotion.controls;
  }
  //Get vendor List Starts
  getVendorList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfVendors' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.vendorList = response.response;
          console.log("Vendor List", JSON.stringify(this.vendorList))
          this.vendorListResponse = true;
        }
        else {
          this.vendorListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  //Get vendor List Ends

  onStartDateSelected(startDate) {
    this.endMin = startDate;
  }

  onEndDateSelected(endDate) {
    this.startMax = endDate;
  }

  cancel() {
    this.router.navigate(['/promotion/listPromotion'])
  }

  load() {
    this.router.navigate(['/promotion/loadToSocialMedia'])
  }


  onSubmit() {
    this.submitted = true;
    //if(this.submitted) return;
    if (!this.addPromotion.value.promotionName) {
      return;
    } else if (!this.addPromotion.value.startDate) {
      return;
    } else if (!this.addPromotion.value.endDate) {
      return;
    } else if (!this.addPromotion.value.marketingMediaType) {
      return;
    } else if (!this.addPromotion.value.description) {
      return;
    } else if (!this.addPromotion.value.promotionFees) {
      return;
    } else if (!this.addPromotion.value.promotionValues) {
      return;
    } else if (!this.addPromotion.value.mode) {
      return;
    } else if (!this.addPromotion.value.type) {
      return;
    } else if (!this.addPromotion.value.totalTicket) {
      return;
    } else if (!this.addPromotion.value.recurring) {
      return;
    } else {
      this.savePromotion()
    }
  }

  savePromotion() {
   if(this.filesToUpload.length > 0 && this.uploadFlag == false) {
  this.displyFlag = true;
     return false;
   }
    let addObj = {
      "promotionCode": this.promotionCode,
      "proId": this.prozData,
      "vendorName": '' + this.addPromotion.value.vendorName,
      "promotionName": this.addPromotion.value.promotionName,
      "startDate": '' + this.addPromotion.value.startDate,
      "endDate": '' + this.addPromotion.value.endDate,
      "type": '' + this.addPromotion.value.type,
      "mode": '' + this.addPromotion.value.mode,
      "recurring": '' + this.addPromotion.value.recurring,
      "promotionFees": '' + this.addPromotion.value.promotionFees,
      "promotionValues": '' + this.addPromotion.value.promotionValues,
      "marketingMediaType": '' + this.addPromotion.value.marketingMediaType,
      "totalTicket": '' + this.addPromotion.value.totalTicket,
      "description": '' + this.addPromotion.value.description,
      "promotionImage": this.imageArray,
      "status": '' + this.addPromotion.value.status,
      "createdBy": this.loginData.emailId
    }
    console.log("ADD OBJ" + JSON.stringify(addObj))
    this.http.post(Global.url + '/addPromotions', addObj)
      .subscribe(data => {
        var promotionData = data.json();
        console.log("customerData" + JSON.stringify(promotionData))
        if (promotionData.status == 200) {
          this.router.navigate(['/promotion/listPromotion'])
        }

      }, error => {
        console.log(error);// Error getting the data
      });
  }


  onSelectFile(fileInput) {
    // this.arr = []
    this.imageArray = []
    this.progressHide1 = false
    // console.log("fileInput.target.files" + fileInput.target.files)
    // console.log("Array checking" + fileInput.target.files.length)
    if (fileInput.target.files.length == 1) {
      this.filesToUpload.push(fileInput.target.files[0])
      this.chosseTypecount = this.filesToUpload.length
    } else {
      for (var i = 0; i < fileInput.target.files.length; i++) {
        this.filesToUpload.push(fileInput.target.files[i])
        console.log("this.filesTOUPLOAD" + JSON.stringify(this.filesToUpload.length))
        this.chosseTypecount = this.filesToUpload.length
        this.fileName = "files"
        console.log("this.chosseTypecount" + this.fileName)
      }
    }
    var filesAmount = fileInput.target.files.length;
       for (let i = 0; i < filesAmount; i++) {
      var reader = new FileReader();
      reader.onload = (fileInput: any) => {
        this.urls.push(fileInput.target.result);
         this.arr.push({ name: fileInput.target.result });
        this.imageuploadstatus = true;
      }

      reader.readAsDataURL(fileInput.target.files[i]);
    }
  // this.uploadData()
  
  }

  uploadData() {

    const files: Array<File> = this.filesToUpload;
     this.formData = new FormData();
    for (let i = 0; i < files.length; i++) {
      this.formData.append("fileImage", files[i]);
    }
    console.log("ddd"+this.formData)
    this.http.post(Global.url + '/multiData', this.formData)
      .map(files => files.json())
      .subscribe(files => {
        for (var i = 0; i < files.imagePath.length; i++) {
          this.newArray.push(files.imagePath[i])
     
        }
        const uniqueArray = Array.from(new Set(this.newArray));
      
        this.imageNames = Object.keys(uniqueArray).map(key => ({ type: key, image: uniqueArray[key] }));
        this.arr = Object.keys(uniqueArray).map(key => ({ type: key, name: Global.url + "/" + uniqueArray[key] }));
      }
      )
  }

  removeSelectedFile(i) {
    
    this.arr.splice(i, 1);
    this.imageArray.splice(i, 1);
    this.filesToUpload.splice(i,1) 
  
  }
  upload() {
    
    console.log("upload is calling")
    const files: Array<File> = this.filesToUpload;
    console.log("files.lenght" + files)

    this.formData = new FormData();
    
    for (let i = 0; i < files.length; i++) {
    
      this.formData.append("fileImage", files[i]);

    }

    this.http.post(Global.url + '/multiData', this.formData)
      .map(files => files.json())
      .subscribe(files => {
        this.imageArray = []
        for (var i = 0; i < files.imagePath.length; i++) {
          this.imageArray.push(files.imagePath[i])
         
        }
        this.uploadFlag = true;
        this.displyFlag = false;
        this.progressHide1 = true;
        this.progressShow1 = false;
        if (this.percentage1 === 100) {
          this.progressHide1 = false;
          this.progressShow1 = true;
          this.img1 = false;
        }
      }
      )
  }

  // LIST Promotion Marketing Media STARTS
  listPromotionMarketingMediaType() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(Global.url + "/getSourceType/" + this.loginData.loginCode, options)
      .subscribe((data) => {
        var marketingMediaType = data.json();
        if (marketingMediaType.status == 200) {
          console.log("Promotion Marketing Media" + JSON.stringify(marketingMediaType));
          this.promotionMarketingMediaType = marketingMediaType.response;
          console.log("list" + JSON.stringify(this.promotionMarketingMediaType));

          // return false;
        } else {

        }
      });
    // , error => {
    // //  this.toastr.warning('Unable to connect to server');
    // })
  }
  //LIST Promotion Marketing Media ENDS

  sendEmails() {
    console.log("Promotion Code ");
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    let emailSendObj = {
      emailSubscription: 'YES',
      proId: this.loginData.loginCode,
    }
    this.http.post(Global.url + "/sendCustomerEmails", emailSendObj, options).subscribe(
      data => {
        var response = data.json();
      },
      error => {
        this.toastr.warning("Unable to connect to server");
      }
    );
  }

  list() {
    this.router.navigate(['/promotion/listPromotion'])
  }
  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
