import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../utilities/imageupload'
import { Global } from '../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
@Component({
  providers: [Beyond],
  selector: 'app-email-list',
  templateUrl: './email-list.component.html',
  styleUrls: ['./email-list.component.scss']
})
export class EmailListComponent implements OnInit {
  loginData: any;
  promotionListDetails: any;
  promotionListResponse: boolean;
  All: boolean;
  searchVendor: FormGroup;
  customerList: any;
  customerSatus: boolean;
  emailListResponse: boolean;
  searchCustomer: FormGroup;
  customerId: string;
  customer: any;
  customerInfoList: any;
  listcustomer: any;
  Customer: boolean;
  Name: boolean;
  PhoneNumber: boolean;
  Email: boolean;
  Address: boolean;
  Status: boolean;
  customerNameValue: any;
  customerArray: any = [];
  proId: void;
  form: FormGroup;
  FirstName: boolean;
  EmailSubscription: boolean;
  LastName: boolean;

  constructor(public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,) {

  }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proId = this.loginData.loginCode;
    this.listCustomerDetails()
    this.All = true;
    {
      this.searchCustomer = this.formBuilder.group({
        firstName: [null],
        lastName: [null],
        phoneNumber: [null],
        emailId: [null],
        emailSubscription: [null],
        selectBy: [null]
      })
    }
  }

  customerTypeSelection(x) {
    console.log("x" + x)
    if (x == "All") {
      this.listCustomerDetails()
    }

    if (x == "FirstName") {
      this.FirstName = true;
      this.PhoneNumber = false;
      this.Email = false;
      this.LastName = false;
      this.EmailSubscription = false;
      this.All = false;
    }
    if (x == "PhoneNumber") {
      this.FirstName = false;
      this.PhoneNumber = true;
      this.Email = false;
      this.LastName = false;
      this.EmailSubscription = false;
      this.All = false;
    }
    if (x == "Email") {
      this.FirstName = false;
      this.PhoneNumber = false;
      this.Email = true;
      this.LastName = false;
      this.EmailSubscription = false;
      this.All = false;
    }
    if (x == "EmailSubscription") {
      this.FirstName = false;
      this.PhoneNumber = false;
      this.Email = false;
      this.LastName = false;
      this.EmailSubscription = true;
      this.All = false;
    }
    if (x == "LastName") {
      this.FirstName = false;
      this.PhoneNumber = false;
      this.Email = false;
      this.LastName = true;
      this.EmailSubscription = false;
      this.All = false;
    }
  }

  reset() {
    this.searchCustomer.reset();
    this.listCustomerDetails()
  }

  listCustomerDetails() {
    // this.spinner.show();
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/vancustomerList/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var customerData = data.json();
        console.log("customerData" + JSON.stringify(customerData))
        if (customerData.status == 200) {
          // this.spinner.hide();
          this.customerList = customerData.addressInfo;
          this.emailListResponse = true;
          return false;
        } else {
          console.log("ELSE CONDITION IS CALLING")
          // this.spinner.hide();
          this.emailListResponse = false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }


  // Search Customer Starts
  searchCustomerData() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let obj = {
      "firstName": this.searchCustomer.value.firstName,
      "lastName": this.searchCustomer.value.lastName,
      "emailId": this.searchCustomer.value.emailId,
      "phoneNumber": this.searchCustomer.value.phoneNumber,
      "emailSubscription": this.searchCustomer.value.emailSubscription
    }
    console.log(obj)
    this.http.post(Global.url + '/searchCustomerEmail/' + this.proId, [
      { id: 'firstName', value: obj.firstName },
      { id: 'lastName', value: obj.lastName },
      { id: 'emailId', value: obj.emailId },
      { id: 'phoneNumber', value: obj.phoneNumber },
      { id: 'emailSubscription', value: obj.emailSubscription }
    ]).subscribe((data: any) => {
      this.customer = data.json();
      console.log("center" + JSON.stringify(this.customer))
      if (this.customer.status == 200) {
        this.customerList = this.customer.response;
        this.emailListResponse = true;
        console.log("customerData values" + JSON.stringify(this.customerList))
        this.searchCustomer.reset();
      } else {
        this.emailListResponse = false;
        console.log('The else')
      }
    })
  }
  // Search Customer Ends
  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  updateList() {
    this.listCustomerDetails()
  }
}
