import { Component, OnInit, ViewChild } from "@angular/core";
import { Beyond } from "../../../../utilities/beyond";
import { Router, ActivatedRoute } from "@angular/router";
import { ImageUpload } from "../../../../utilities/imageupload";
import { Global } from "../../../../global/global";
import { Http, Headers, RequestOptions } from "@angular/http";
import { ToastrService } from "ngx-toastr";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
  FormArray
} from "@angular/forms";
import * as moment from 'moment';

@Component({
  providers: [Beyond, ImageUpload],
  selector: 'app-edit-promotion',
  templateUrl: './edit-promotion.component.html',
  styleUrls: ['./edit-promotion.component.scss']
})
export class EditPromotionComponent implements OnInit {
  editPromotion: FormGroup;
  loginEmailData: any;
  prozData: any;
  loginData: any;
  promotions: boolean;
  promotionList: any;
  vendorList: any;
  vendors: boolean;
  percentage: any = 0;
  imagePreview: string = "../../assets/img/no-image.png";
  file2Upload: any
  documentName: any;
  img: boolean = false;
  @ViewChild('imageUpload')
  imageUploadVariable: any;
  progressShow: boolean = false;
  progressHide: boolean = false;
  viewPromotionData: any;
  promotionImg: any;
  vendorListResponse: boolean;
  submitted: boolean = false;
  min: any = new Date();
  startMin: any = moment().format('YYYY-MM-DD');
  startMax: any;
  endMin: any = moment().format('YYYY-MM-DD');
  promoCode: any;
  urls = [];
  imagePreviewDocument: string = "assets/img/no-image.png";
  galleryImages = [];
  filesToUpload: any = [];
  imageArray: any = []
  progressShow1: boolean = false;
  progressHide1: boolean = false;
  img1: boolean = false;
  percentage1: any = 0;
  file2Upload1: any;
  file: any;
  arr: any = [];
  imageuploadstatus: boolean;
  chosseTypecount: number;
  fileName: string;
  formData: FormData;
  chooseStatus: boolean = true
  value: any;
  removedPiture: any;
  result: any = [];
  imageNameData: any = [];
  newArray: any = [];
  number: any;
  renamedarray: any;
  orginalArray: any = [];
  value1: any;
  imageName: any = [] = [];
  newValue: any;
  resultData: { index: string; value: any; }[];
  imageNames: any = [];
  speArray: string;
  imageValue: any = [];
  promotionMarketingMediaType: any;
  displyFlag: boolean = false;
  uploadFlag: boolean = false;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.promoCode = this.route.snapshot.params['promotionCode'];
    this.loginData = JSON.parse(localStorage.getItem("login"));
    this.prozData = this.loginData.loginCode;
    this.loginEmailData = this.loginData.emailId;
    this.getVendorList(),
      this.listPromotionMarketingMediaType(),
      this.viewPromotions()
    this.editPromotion = this.formBuilder.group({
      promotionCode: [''],
      proId: [''],
      vendorName: [''],
      promotionName: ['', Validators.required],
      startDate: ['', Validators.required],
      endDate: ['', Validators.required],
      type: ['', Validators.required],
      mode: ['', Validators.required],
      recurring: ['', Validators.required],
      promotionFees: ['', Validators.required],
      promotionValues: ['', Validators.required],
      marketingMediaType: ['', Validators.required],
      totalTicket: ['', Validators.required],
      description: ['', Validators.required],
      promotionImage: [''],
      status: ['']
    })
  }

  cancel() {
    this.router.navigate(['/promotion/listPromotion'])
  }

  //Get vendor List Starts
  getVendorList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfVendors' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.vendorList = response.response;
          console.log("Vendor List", JSON.stringify(this.vendorList))
          this.vendorListResponse = true;
        }
        else {
          this.vendorListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }


  //Get vendor List Ends

  // LIST Promotion Marketing Media STARTS
  listPromotionMarketingMediaType() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(Global.url + "/getSourceType/" + this.loginData.loginCode, options)
      .subscribe((data) => {
        var marketingMediaType = data.json();
        if (marketingMediaType.status == 200) {
          console.log("Promotion Marketing Media" + JSON.stringify(marketingMediaType));
          this.promotionMarketingMediaType = marketingMediaType.response;
          console.log("list" + JSON.stringify(this.promotionMarketingMediaType));

          // return false;
        } else {

        }
      });
    // , error => {
    // //  this.toastr.warning('Unable to connect to server');
    // })
  }
  //LIST Promotion Marketing Media ENDS
  onStartDateSelected(startDate) {
    this.endMin = startDate;
  }

  onEndDateSelected(endDate) {
    this.startMax = endDate;
  }

  get f() {
    return this.editPromotion.controls;
  }

  onSubmit() {
    this.submitted = true;
    if (!this.editPromotion.value.promotionName) {
      return;
    } else if (!this.editPromotion.value.startDate) {
      return;
    } else if (!this.editPromotion.value.endDate) {
      return;
    } else if (!this.editPromotion.value.marketingMediaType) {
      return;
    } else if (!this.editPromotion.value.description) {
      return;
    } else if (!this.editPromotion.value.promotionFees) {
      return;
    } else if (!this.editPromotion.value.promotionValues) {
      return;
    } else if (!this.editPromotion.value.mode) {
      return;
    } else if (!this.editPromotion.value.type) {
      return;
    } else if (!this.editPromotion.value.totalTicket) {
      return;
    } else if (!this.editPromotion.value.recurring) {
      return;
    } else {
      this.updatePromotion()
    }
  }


  updatePromotion() {
    if (this.filesToUpload.length > 0 && this.uploadFlag == false) {
      this.displyFlag = true;
      return false;
    }
    let editObj = {
      "promotionCode": this.promoCode,
      "proId": this.prozData,
      "vendorName": '' + this.editPromotion.value.vendorName,
      "promotionName": '' + this.editPromotion.value.promotionName,
      "startDate": '' + this.editPromotion.value.startDate,
      "endDate": '' + this.editPromotion.value.endDate,
      "type": '' + this.editPromotion.value.type,
      "mode": '' + this.editPromotion.value.mode,
      "recurring": '' + this.editPromotion.value.recurring,
      "promotionFees": '' + this.editPromotion.value.promotionFees,
      "promotionValues": '' + this.editPromotion.value.promotionValues,
      "marketingMediaType": '' + this.editPromotion.value.marketingMediaType,
      "totalTicket": '' + this.editPromotion.value.totalTicket,
      "description": '' + this.editPromotion.value.description,
      "promotionImage": this.imageArray,
      "status": this.editPromotion.value.status,
      "modifiedBy": this.loginEmailData
    }
    console.log("Edit OBJ" + JSON.stringify(editObj))
    this.http.post(Global.url + '/updatePromotions', editObj)
      .subscribe(data => {
        var promotionData = data.json();
        console.log("edit Promotion Data" + JSON.stringify(promotionData))

        if (promotionData.status == 200) {
          this.router.navigate(['/promotion/listPromotion'])
          if (this.editPromotion.value.promotionImage == '' || this.editPromotion.value.promotionImage == null || this.editPromotion.value.promotionImage == "undefined") {

            this.imagePreview = "../../assets/img/no-image.png"
          } else {
            for (var i = 0; i < this.urls.length; i++) {
              this.imagePreview = Global.url + "/" + this.urls[i]
              this.arr.push({ name: this.imagePreview });
              this.arr.push(this.urls[i])
              this.imageArray.push(Global.url + "/" + this.urls[i])
              console.log("THSISSSSS" + JSON.stringify(this.arr))
              //  this.imageArray.push(Global.url + files.imagePath[i])


            }
          }
        }


      }, error => {
        console.log(error);// Error getting the data
      });
  }

  // start view Vendor
  viewPromotions() {
    //this.spinner.show()
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewPromotion/' + this.promoCode, options)
      .subscribe(data => {
        var promotion = data.json();
        if (promotion.status == 200) {
          // this.spinner.hide()
          this.viewPromotionData = promotion.response[0];
          this.viewPromotionData.status == 'ACTIVE' ? this.editPromotion.enable() : this.editPromotion.disable()
          console.log("Promotion Data", JSON.stringify(this.viewPromotionData))
        }
        else {
        }

        this.editPromotion = this.formBuilder.group({
          "vendorName": [this.viewPromotionData.vendorName, Validators.required],
          "promotionName": [this.viewPromotionData.promotionName, Validators.required],
          "startDate": [this.viewPromotionData.startDate, Validators.required],
          "endDate": [this.viewPromotionData.endDate, Validators.required],
          "type": [this.viewPromotionData.type, Validators.required],
          "mode": [this.viewPromotionData.mode, Validators.required],
          "recurring": [this.viewPromotionData.recurring, Validators.required],
          "promotionFees": [this.viewPromotionData.promotionFees, Validators.required],
          "promotionValues": [this.viewPromotionData.promotionValues, Validators.required],
          "marketingMediaType": [this.viewPromotionData.marketingMediaType, Validators.required],
          "totalTicket": [this.viewPromotionData.totalTicket, Validators.required],
          "description": [this.viewPromotionData.description, Validators.required],
          "status": [this.viewPromotionData.status, Validators.required],
        });
        this.chooseStatus = this.viewPromotionData.status == 'ACTIVE' ? false : true;
        this.urls = this.viewPromotionData.promotionImage.split(',')
        if (this.viewPromotionData.promotionImage == "" || this.viewPromotionData.promotionImage == null || this.viewPromotionData.promotionImage == undefined || this.viewPromotionData.promotionImage == "undefined") {
          this.imagePreview = '../../assets/img/no-image.png'
          var index = this.imagePreview.indexOf(this.imagePreview)
          this.removeSelectedFile(index)
        }
        else {

          for (var i = 0; i < this.urls.length; i++) {
            this.imageArray.push(this.urls[i])
            this.imageNames.push({ image: this.urls[i] });
            this.imagePreview = Global.url + "/" + this.urls[i]
            this.arr.push({ name: this.imagePreview });
          }
          this.fileName = this.imageArray.length
          console.log('The execution is fileName ' + JSON.stringify(this.fileName))
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  // end view Vendor
  load() {
    this.router.navigate(['/promotion/loadToSocialMedia'])
  }

  onSelectFile(fileInput) {
    // this.arr = []
    //this.imageArray = []
    this.progressHide1 = false
    console.log("fileInput.target.files" + fileInput.target.files)
    console.log("Array checking" + fileInput.target.files.length)
    if (fileInput.target.files.length == 1) {
      this.filesToUpload.push(fileInput.target.files[0])
      this.chosseTypecount = this.filesToUpload.length
    } else {
      for (var i = 0; i < fileInput.target.files.length; i++) {
        this.filesToUpload.push(fileInput.target.files[i])
        console.log("this.filesTOUPLOAD" + JSON.stringify(this.filesToUpload.length))
        this.chosseTypecount = this.filesToUpload.length
        this.fileName = "files"
        console.log("this.chosseTypecount" + this.fileName)
      }
    }
    var filesAmount = fileInput.target.files.length;
    console.log("filesAmount" + filesAmount)
    for (let i = 0; i < filesAmount; i++) {
      var reader = new FileReader();
      reader.onload = (fileInput: any) => {
        console.log(fileInput.target.result);
        this.urls.push(fileInput.target.result);
        this.arr.push({ name: fileInput.target.result });
        // console.log("checking data" + this.urls.length)
        // console.log("array names" + JSON.stringify(this.arr))

        this.imageuploadstatus = true;

      }
      console.log("array names" + JSON.stringify(this.arr))
      reader.readAsDataURL(fileInput.target.files[i]);
    }
    // this.uploadData()
  }

  removeSelectedFile(i) {
    this.arr.splice(i, 1);
    this.imageArray.splice(i, 1);
    this.filesToUpload.splice(i, 1)
    console.log(this.imageArray)
    // this.imageArray = []
    // for (var p = 0; p < this.arr.length; p++) {
    //   this.value = this.arr[p].name
    //   this.imageArray.push(this.arr[p].name.substring(25, 50))
    //  
    // }
  }


  removeDuplicates() {
    this.result.filter((value, index) => this.result.indexOf(value) === index)

    var number = this.result.indexOf()
    console.log("IMAGEARRAY" + JSON.stringify(this.result.indexOf()))
    console.log("number" + number)
    if (number > -1) {
      this.result.splice(number, 1);
    }
    this.imageArray.push(this.result)
    console.log("imageArrayimageArray" + JSON.stringify(this.imageArray))

  }
  upload() {
    const files: Array<File> = this.filesToUpload;
    this.formData = new FormData();
    for (let i = 0; i < files.length; i++) {
      this.formData.append("fileImage", files[i]);
    }
    this.http.post(Global.url + '/multiPromotionData', this.formData)
      .map(files => files.json())
      .subscribe(files => {

        // this.imageArray = []
        for (var i = 0; i < files.imagePath.length; i++) {
          this.imageArray.push(files.imagePath[i])
          console.log("  this.imageArray" + JSON.stringify(this.imageArray))
        }
        this.uploadFlag = true;
        this.displyFlag = false;
        this.progressHide1 = true;
        this.progressShow1 = false;
        if (this.percentage1 === 100) {
          this.progressHide1 = false;
          this.progressShow1 = true;
          this.img1 = false;
        }
      }
      )
  }

  uploadData() {
    const files: Array<File> = this.filesToUpload;
    this.formData = new FormData();
    for (let i = 0; i < files.length; i++) {
      this.formData.append("fileImage", files[i]);
    }
    this.http.post(Global.url + '/multiPromotionData', this.formData)
      .map(files => files.json())
      .subscribe(files => {
        for (var i = 0; i < files.imagePath.length; i++) {
          this.newArray.push(files.imagePath[i])
        }
        const uniqueArray = Array.from(new Set(this.newArray));
        this.imageNames = Object.keys(uniqueArray).map(key => ({ type: key, image: uniqueArray[key] }));
        this.arr = Object.keys(uniqueArray).map(key => ({ type: key, name: Global.url + "/" + uniqueArray[key] }));
      }
      )
  }

  /* Image Upload */

  status(chooseStatus) {
    if (chooseStatus == false) {
      this.editPromotion.enable()
      this.editPromotion.get('status').setValue('ACTIVE')
    }
    else if (chooseStatus == true) {
      this.editPromotion.disable()
      this.editPromotion.get('status').setValue('INACTIVE')
    }
  }

}

