import { Component, OnInit, ViewChild } from "@angular/core";
import { Beyond } from "../../../../utilities/beyond";
import { Router, ActivatedRoute } from "@angular/router";
import { ImageUpload } from "../../../../utilities/imageupload";
import { Global } from "../../../../global/global";
import { Http, Headers, RequestOptions } from "@angular/http";
import { ToastrService } from "ngx-toastr";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
  FormArray
} from "@angular/forms";
declare var $: any;
@Component({
  providers: [Beyond, ImageUpload],
  selector: 'app-view-promotion',
  templateUrl: './view-promotion.component.html',
  styleUrls: ['./view-promotion.component.scss']
})

export class ViewPromotionComponent implements OnInit {
  loginEmailData: any;
  prozData: any;
  loginData: any;
  promotionCode: any;
  promotions: boolean;
  promotionList: any;
  vendorList: any;
  vendors: boolean;
  percentage: any = 0;
  imagePreview: string = "../../assets/img/no-image.png";
  imgPreview: string = "/assets/img/proz/company_default";
  file2Upload: any
  documentName: any;
  img: boolean = false;
  @ViewChild('imageUpload')
  imageUploadVariable: any;
  progressShow: boolean = false;
  progressHide: boolean = false;
  viewPromotionData: any;
  promotionData: any;
  customerData2: string;
  urls = [];
  imagePreviewDocument: string = "assets/img/no-image.png";
  file: any;
  arr: any = [];
  viewPreviewMessageData: any;
  imageArray: any = []
  imageName: any = [];
  showModal: boolean;
  Image: any = [];
  index: any;
  fileName: string;
  // myThumbnail = "https://wittlock.github.io/ngx-image-zoom/assets/thumb.jpg";
  //myFullresImage = "https://wittlock.github.io/ngx-image-zoom/assets/fullres.jpg";
  imageData: any = [];
  imageNames: any = [];
  speArray: string;
  imageValue: any = [];
  withOutImg: boolean;
  withImg: boolean;
  previewMessage: boolean;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.promotionCode = this.route.snapshot.params['promotionCode'];
    console.log("Promotion Code", this.promotionCode)
    this.loginData = JSON.parse(localStorage.getItem("login"))

    this.beyond.beyondAdmin();
    this.viewPromotions()
    this.viewPreviewMessage()
  }

  cancel() {
    this.router.navigate(['/promotion/listPromotion'])
  }
  ok() {
    this.router.navigate(['/promotion/listPromotion'])
  }

  load() {
    this.router.navigate(['/promotion/loadToSocialMedia'])
  }

  // start view Vendor
  viewPromotions() {
    //this.spinner.show()
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewPromotion/' + this.promotionCode, options)
      .subscribe(data => {
        var promotion = data.json();
        if (promotion.status == 200) {
          // this.spinner.hide()
          this.viewPromotionData = promotion.response[0];
          console.log("Promotion Data", JSON.stringify(this.viewPromotionData))
          this.urls = this.viewPromotionData.promotionImage.split(',')
          console.log("Promotion Image" + JSON.stringify(this.urls))
          if (this.viewPromotionData.promotionImage == "" || this.viewPromotionData.promotionImage == null || this.viewPromotionData.promotionImage == "undefined" || this.viewPromotionData.promotionImage == undefined) {
            this.imagePreview = '../../assets/img/no-image.png'
            var index = this.imagePreview.indexOf(this.imagePreview)
            console.log("TTTTTT" + index)
            this.withImg = false;
            this.withOutImg = true;
          }
          else {
            this.withImg = true;
            this.withOutImg = false;
            for (var i = 0; i < this.urls.length; i++) {
              this.imageName.push({ image: this.urls[i] });
              console.log("imageNameimageName" + JSON.stringify(this.imageName))
              this.imagePreview = Global.url + "/" + this.urls[i]
              this.arr.push({ name: this.imagePreview });
            }
          }
        }
        error => {
          this.toastr.warning('Unable to Connect to Server')

        }
      })
  }
  // end view Vendor

  /* Image showing in pop up */
  show(index) {
    console.log("index::::" + JSON.stringify(index.name))
    this.Image = index.name.substring(22, 50)
    console.log("Image::::" + JSON.stringify(this.Image))
    for (var i = 0; i < this.arr.length; i++) {
      this.imagePreview[i]
      console.log("this.arr[i].name:::" + this.arr[i].name)
      if (index.name == this.arr[i].name) {
        this.imagePreview = index.name
        // this.Image//=index.substring(22, 50)
        console.log("imagePreview" + this.Image)
        this.showModal = true;
        $("#myModal0").modal("show");
      }

      else {
        //  $("#myModal0").modal("hide"); 
      }
    }
  }
  /* Image showing in pop up */
  //Bootstrap Modal Close event
  hide() {
    $("#myModal0").modal("hide");
  }

  editPromotion() {
    this.router.navigate(['/promotion/editPromotion', this.promotionCode])
  }

  sendEmails() {
    console.log("Send Emails");
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    let emailSendObj = {
      emailSubscription: 'YES',
      proId: this.loginData.loginCode,

    }
    this.http.post(Global.url + '/sendCustomerEmails/' + this.promotionCode, emailSendObj).subscribe(
      data => {
        var response = data.json();
      },
      error => {
        this.toastr.warning("Unable to connect to server");
      }
    );
  }

  viewPreviewMessage() {
    //this.spinner.show()
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewPreviewMessage/' + this.promotionCode + '/' + this.loginData.loginCode, options).subscribe(data => {
      var preview = data.json();
      if (preview.status == 200) {
        // this.spinner.hide()
        this.viewPreviewMessageData = preview.response[0];
        console.log("Preview Message Data", JSON.stringify(this.viewPreviewMessageData))
        this.viewPreviewMessageData.image == 'assets/img/proz/company_default.jpg' ? this.imgPreview = "../../../assets/img/proz/company_default.jpg" : this.imgPreview = Global.url + this.viewPreviewMessageData.image
        this.previewMessage = true;
      }
      else {
        this.previewMessage = true;
      }
      error => {
        this.toastr.warning('Unable to Connect to Server')

      }
    })
  }
}

