import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListPromotionComponent } from './promotions/list-promotion/list-promotion.component';
import { PromotionsComponent } from './promotions/promotions.component';
import { EditPromotionComponent } from './promotions/edit-promotion/edit-promotion.component';
import { AddPromotionComponent } from './promotions/add-promotion/add-promotion.component';
import { ViewPromotionComponent } from './promotions/view-promotion/view-promotion.component';
import { LoadToSocialMediaComponent } from './promotions/load-to-social-media/load-to-social-media.component';
import { EmailListComponent } from './email-list/email-list.component';
import { BudgetingComponent } from './budgeting/budgeting.component';

const routes: Routes = [

  {
    path: '', component: PromotionsComponent,

    children: [
      {
        path: 'listPromotion',
        component: ListPromotionComponent
      },
      {
        path: 'editPromotion/:promotionCode',
        component: EditPromotionComponent
      },
      {
        path: 'viewPromotion/:promotionCode',
        component: ViewPromotionComponent
      },
      {
        path: 'addPromotion',
        component: AddPromotionComponent
      },
      {
        path: 'loadToSocialMedia',
        component: LoadToSocialMediaComponent
      },
      {
        path: 'emailList',
        component: EmailListComponent
      },
      {
        path: 'budget',
        component: BudgetingComponent
      },


    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MarketingRoutingModule { }
